.header {

    .navigation {
        display: flex;
        align-items: center;
        justify-content: center;

        &__logo {
            flex-shrink: 0;
            width: 94px;
            margin: 9px 28px 0;
        }

        &__left,
        &__right {
            display: flex;
            align-items: center;
            width: calc(50% - 47px);
            flex-grow: 1;
            flex-shrink: 0;
            flex-wrap: wrap;
            padding: 0 0 9px;

            a {
                text-transform: uppercase;
                font-size: 13px;
                font-weight: 700;
                line-height: 16px;
                display: block;
                letter-spacing: 2.5px;
                padding: 5px 35px;
            }
        }

        &__left {
            justify-content: flex-end;
        }
    }
}